<template>
  <div class="home">
    <Infos/>
    <!-- <Article/> -->
  </div>
</template>

<script>
import Infos from "./Infos";
import Article from "./Article";
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "Home",
  data() {
    return {
      name: ["asssss","ddwa","dwafdawf"]
    };
  },
  components: {
    Infos,
    Article
  },
  computed: {
    ...mapGetters(["count"])
  },
  methods: {
    goToShop: function() {
      this.$router.push("/shop");
    },
    getPets: function() {
      let timestamp = Date.now();
      this.$http.get(`http://localhost:3000/api/pets?time=${timestamp}`).then(
        response => {
          // get body data
          this.name = response.body;
        },
        response => {
          // error callback
        }
      );
    },
    createPet: function() {
      let postData = {
        name: "ann",
        gender: true
      };
      this.$http
        .post("http://localhost:3000/api/pets", postData, {
          emulateJSON: true
        })
        .then(
          function(res) {
            document.write(res.body);
          },
          function(res) {
            console.log(res.status);
          }
        );
    },
    ...mapMutations(["increment"])
  },
  props: ["s", "goBack"]
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.home {

}
</style>



