<template>
  <div class="about">
    <h1>hhhhhh</h1>
    <h1>{{s.app1}}</h1>
    <h1>{{count}}</h1>
    <button @click="increment">add 1</button>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "About",
  data() {
    return {
      name: "as"
    };
  },
  computed: {
    ...mapGetters(["count"])
  },
  methods: {
    ...mapMutations(["increment"])
  },
  props: ["s"]
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



