<template>
  <div id="app">
    <div class="header flex">
      <router-link to="/">Home</router-link>
      <router-link to="/Moments">Moments</router-link>
      <router-link to="/Archives">Archives</router-link>
      <router-link to="/Shop">Shop</router-link>
      <router-link to="/About">About</router-link>
    </div>

    <router-view :s="app_store" :go-back="goBack"/>

    <div class="footer flex">
      <div class="description">
        她很忙，她正在死亡
      </div>
      <span @click="join_favorite"><i :class="[isMarked ? 'fa-bookmark' : 'fa-bookmark-o', 'fa']"></i>  &nbsp;{{markText}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      isMarked: true,
      app_store: {}
    };
  },
  methods: {
    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    join_favorite: async function() {
      console.log("shoucang.");
      //捕获加入收藏过程中的异常
      let siteUrl = window.location.href;
      let siteName = "greennn's space";

      try {
        //判断浏览器是否支持document.all
        console.log("try");

        if (document.all) {
          //如果支持则用external方式加入收藏夹
          console.log("all");
          window.external.addFavorite(siteUrl, siteName);
        } else if (window.sidebar) {
          //如果支持window.sidebar，则用下列方式加入收藏夹
          console.log("sidebar");
          window.sidebar.addPanel(siteName, siteUrl, "");
        }
      } catch (e) {
        //处理异常

        alert("加入收藏夹失败，请使用Ctrl+D快捷键进行添加操作!");
      }
      this.isMarked = !this.isMarked;
    }
  },
  computed: {
    markText: function() {
      return this.isMarked ? "" : "Mark it";
    }
  }
};
</script>

<style lang="scss">
@import "./assets/style.scss";
@import "font-awesome/css/font-awesome.css";

#app {
  width: 100%;
  max-width: $max-width;
  box-sizing: border-box;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding-top: 10vh;

  .header {
    max-width: $max-width;
    width: 100%;
    position: fixed;
    top: 0;
    text-align: center;
  }
  .footer {
    $footer-color: #868e96;
    position: fixed;
    bottom: 0;
    width: 100%;
    max-width: 1200px;
    font-size: 12px;
    background: #fff;
    color: $footer-color;
    .description {
      text-align: center;
      padding: 2rem;
    }
    span {
      .fa {
        cursor: pointer;
      }
      .fa-bookmark {
        color: $my-color;
        &:hover {
          color: $footer-color;
        }
      }
      .fa-bookmark-o {
        &:hover {
          color: $my-color;
        }
      }
    }
    i {
    }
  }
}
</style>



