<template>
  <div class="infos">
    <div class="flex-column">
      <img src="../assets/logo.png" alt="greennn's pic">
      <h1>Greennn</h1>
      <div class="description">
        你也网上冲浪啊：）
      </div>
      <div class="menu flex">
        <a @click.prevent="goTo('/')">Home</a>
        <a @click.prevent="goTo('/Moments')">Moments</a>
        <a @click.prevent="goTo('/Archives')">Archives</a>
        <a @click.prevent="goTo('/Shop')">Shop</a>
        <a @click.prevent="goTo('/About')">About</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Infos",
  data() {
    return {
      
    };
  },
  computed: {
    
  },
  methods: {
    goTo: function(url) {
      this.$router.push(url);
    },
  },
  props: []
};
</script>

<style scoped lang="scss">
.infos {
  .flex-column {
    min-height: 60vh;
  }
  img {
    width: 120px;
    height: 120px;
  }
  h1 {
    line-height: 2rem;
    margin: 0;
  }
  .description {
    padding: 2rem;
    font-weight: 300;
  }
}
</style>



