<template>
  <div class="shop">
    <h1>Article</h1>
    <div>{{name}}</div>
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      name: "as"
    };
  },
  computed: {},
  methods: {},
  props: {
    s: Object,
    goBack: Function
  },
  created: function(){
    let that = this;
    (async ()=>{
      that.$http.get("http://localhost:3000/api/pets").then(
        response => {
          // get body data
          that.name = response.body;
        },
        response => {
          // error callback
        }
      );
    })()
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



